<template xmlns:v-finger="http://www.w3.org/1999/xhtml">
  <div id="carousel">
    <div id="main" v-finger:swipe="runCarousel">
      <div class="main-wrapper" :style="wrapperStyle">
        <div class="main-item pointer left" :style="sideStyle1" @click="article(index)">
          <p v-if="sideItem('left').caption !== ''">{{sideItem('left').caption}}</p>
          <p v-if="sideItem('left').caption2 !== ''">{{sideItem('left').caption2}}</p>
        </div>
        <div v-for="(item, index) in renderItems" v-bind:key="index" class="main-item pointer left" :style="itemStyle" @click="article(index)" v-img="{'bg': item.bg, 'index': index}">
          <p v-if="item.caption !== ''">{{item.caption}}</p>
          <p v-if="item.caption2 !== ''">{{item.caption2}}</p>
        </div>
        <div class="main-item pointer left" :style="sideStyle2" @click="article(index)">
          <p v-if="sideItem('right').caption !== ''">{{sideItem('right').caption}}</p>
          <p v-if="sideItem('right').caption2 !== ''">{{sideItem('right').caption2}}</p>
        </div>
      </div>
      <div class="main-index">
        <div v-for="index in renderItems.length" v-bind:key="index" class="main-index-item" @click="carousel(index)">
          <div class="index-circle pointer" :class="{'active': circleStyle(index)}"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'carousel',
  data () {
    return {
      carouselIndex: 1,
      interval: [],
      timeout: [],
      restart: false,
      loadedIndex: 0,
      annualRun: false
    }
  },
  props: {
    mainItem: {
      type: Array
    }
  },
  methods: {
    carousel0: function (i) {
      clearTimeout(this.timeout)
      var self = this
      if (this.restart) {
        this.restart = false
      }
      this.carouselIndex = i
      if (this.carouselIndex > this.renderItems.length) {
        this.timeout = setTimeout(function () {
          self.restart = true
          self.carouselIndex = 1
        }, 400)
      } else if (this.carouselIndex < 1) {
        this.timeout = setTimeout(function () {
          self.restart = true
          self.carouselIndex = self.renderItems.length
        }, 400)
      }
    },
    carousel: function (i) {
      var self = this
      this.annualRun = true
      clearInterval(this.interval)
      this.carousel0(i)
      this.interval = setInterval(function () {
        self.carousel0(self.carouselIndex + 1)
      }, 5400)
    },
    article: function (i) {
      this.$root.article = this.renderItems[i]
      this.$router.push({name: 'art', query: {articleId: this.renderItems[i].id}})
    },
    circleStyle: function (i) {
      if (this.carouselIndex === i) {
        return true
      } else if (this.carouselIndex === this.renderItems.length + i) {
        return true
      } else if (this.carouselIndex === 0 && i === 0) {
        return true
      } else {
        return false
      }
    },
    runCarousel: function (ev) {
      switch (ev.direction) {
        case 'Right':
          ev.stopPropagation()
          if (this.carouselIndex >= 1) {
            this.carousel(--this.carouselIndex)
          }
          break
        case 'Left':
          ev.stopPropagation()
          if (this.carouselIndex <= this.renderItems.length) {
            this.carousel(++this.carouselIndex)
          }
          break
      }
    },
    sideItem: function (arg) {
      var temp
      switch (arg) {
        case 'left':
          temp = this.renderItems[this.renderItems.length - 1]
          if (typeof (temp) === 'undefined') {
            return {id: -1, caption: '', caption2: '加载中...', bg: ''}
          } else {
            return temp
          }
        case 'right':
          temp = this.renderItems[0]
          if (typeof (temp) === 'undefined') {
            return {id: -1, caption: '', caption2: '加载中...', bg: ''}
          } else {
            return temp
          }
      }
    }
  },
  computed: {
    renderItems: function () {
      return this.mainItem
    },
    wrapperStyle: function () {
      return {
        width: (this.renderItems.length + 2) * 100 + '%',
        transition: this.wrapperTransition,
        transform: this.wrapperTransform
      }
    },
    wrapperTransition: function () {
      return this.restart ? '' : 'transform .4s cubic-bezier(0.4, 0, 0, 1)'
    },
    wrapperTransform: function () {
      return 'translate3d(' + -this.carouselIndex * 100 / (this.renderItems.length + 2) + '%, 0, 0)'
    },
    sideStyle1: function () {
      return 'width: ' + 100 / (this.renderItems.length + 2) + '%; ' + this.sideBackground1
    },
    sideStyle2: function () {
      return 'width: ' + 100 / (this.renderItems.length + 2) + '%; ' + this.sideBackground2
    },
    itemStyle: function () {
      return 'width: ' + 100 / (this.renderItems.length + 2) + '%; '
    },
    sideBackground1: function () {
      return this.loadedIndex === this.mainItem.length && this.loadedIndex > 1 ? this.renderItems[this.loadedIndex - 1].bg : 'background: #' + Math.floor(Math.random() * 1000000)
    },
    sideBackground2: function () {
      return this.loadedIndex > 0 ? this.mainItem[0].bg : 'background: #' + Math.floor(Math.random() * 1000000)
    }
  },
  watch: {
    mainItem: function () {
      $('.main-index').css('width', this.renderItems.length * 30 + 'px')
      $('.main-index').css('margin', '0 calc((100% - ' + this.renderItems.length * 30 + 'px) / 2)')
      var temp, height, list, text, j, i
      if (!this.$root.adapt) {
        temp = this.$root.carousel
        $('#main').css('height', temp)
        $('.main-item').css('height', temp)
      } else {
        temp = $('#main').height()
      }
      list = $('.main-item')
      for (j = 0; j < list.length; j++) {
        text = list.eq(j).children('p')
        height = 0
        for (i = 0; i < text.length; i++) {
          height += text.eq(i).height()
        }
        $('.main-item').eq(j).children('p').eq(0).css('margin-top', (temp - height) / 2)
      }
    },
    loadedIndex: function (val) {
      if (val === this.mainItem.length && !this.annualRun) {
        this.carousel(1)
      }
    }
  },
  directives: {
    img: {
      inserted: function (el, binding, vnode) {
        // eslint-disable-next-line
        var temp = binding.value.bg.split(/[\(\)]/)[1]
        if (typeof (temp) !== 'undefined') {
          var color = Math.floor(Math.random() * 1000000)
          el.style.background = '#' + color
          if (binding.value.index > vnode.context._data.loadedIndex) {
            const context = vnode.context
            var interval = setInterval(function () {
              if (binding.value.index === context._data.loadedIndex) {
                var img = new Image()
                img.src = temp.substring(1, temp.length - 1)
                img.onload = function () {
                  var original = el.style.cssText
                  el.style.cssText = original + binding.value.bg
                  context._data.loadedIndex = binding.value.index + 1
                }
                clearInterval(interval)
              }
            }, 100)
          } else {
            var img = new Image()
            img.src = temp.substring(1, temp.length - 1)
            img.onload = function () {
              var original = el.style.cssText
              el.style.cssText = original + binding.value.bg
              vnode.context._data.loadedIndex = binding.value.index + 1
            }
          }
        }
      }
    }
  },
  beforeDestroy () {
    clearInterval(this.interval)
    clearTimeout(this.timeout)
  }
}
</script>

<style scoped>
  #main{
    background-size: cover;
    background-position: 50% 50%;
    overflow: hidden;
    position: relative;
  }
  .main-wrapper{
    height: 100%;
    position: absolute;
  }
  .main-item{
    height: 100%;
    text-align: center;
  }
  .main-item p{
    color: white;
  }
  .main-item p:last-of-type{
    display: inline-block;
  }
  .main-item:first-of-type p:last-of-type{
    font-family: Xingkai!important;
  }
  .main-index{
    height: 30px;
    overflow: hidden;
    position: absolute;
  }
  .main-index-item{
    height: 30px;
    float: left;
  }
  .index-circle{
    border: 2px solid #DCDCDC;
    border-radius: 50%;
  }
  .index-circle{
    width: 12px;
    height: 12px;
    margin: 7px;
  }
  .index-circle.active{
    border-color: white;
  }

  @media only screen and (max-device-width: 767px) {
    .main-index{
      bottom: 12px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .main-index{
      bottom: 30px;
    }
  }
</style>



