<template>
  <div id="center" @touchstart="hideList">
    <Carousel :main-item="mainItem"></Carousel>
    <div class="main-content">
      <div v-for="(item, index) in mainContent" v-bind:key="index" class="page" :style="renderPage(index)">
        <p v-if="item.caption !== ''" class="title">{{item.caption}}</p>
        <p v-if="item.caption2 !== ''" class="interpret">{{item.caption2}}</p><br>
        <p v-if="item.learn" class="learnMore pointer" @click="learn(index)">了解更多</p>
        <div v-if="index === 0" id="solution-bar" class="solution-circle-bar">
          <div class="solution-circle-wrapper">
            <transition-group name="solution">
              <div v-for="(item, index) in solutionList" v-bind:key="index" class="solution-circle pointer left">
                <p>{{item}}</p>
              </div>
            </transition-group>
          </div>
        </div>
      </div>
      <foot></foot>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Carousel from './Carousel'
import foot from './Foot'
export default {
  name: 'center',
  data () {
    return {
      mainItem: [],
      mainContent: [
        {caption: '全面、领先的解决方案', caption2: '产品覆盖数通安全、传输接入、IT、企业云通信、网络能源、云软件，能针对不同需求提供完整的解决方案。', learn: true},
        {caption: '丰富的项目经验', caption2: '多年来，凭借卓越的产品和优秀的服务，公司已完成A、B、C等多个大型项目建设。'},
        {caption: '专业、可靠的技术支持', caption2: '专业的技术团队为政府、企业的数字化、智慧化转型保驾护航。'},
        {caption: '卓越的公司团队', caption2: '', learn: true}
      ],
      solutions: [
        '数通安全',
        '传输输入',
        'IT',
        '企业云通信',
        '网络能源',
        '云软件'
      ],
      solutionList: [],
      solutionIndex: 1,
      solutionStatus: false
    }
  },
  methods: {
    renderPage: function (i) {
      if (i % 2 === 0) {
        return 'background-color: white;'
      } else {
        return 'background-color: #F0F2F5;'
      }
    },
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    },
    learn: function (i) {
      switch (i) {
        case 0:
          this.$router.push({path: '/products'})
          break
        case 3:
          this.$router.push({path: '/introduction'})
          break
      }
    },
    showSolution: function () {
      var self = this
      for (let i = 0; i < this.solutions.length; i++) {
        setTimeout(function () {
          self.solutionList.push(self.solutions[i])
          self.$nextTick()
        }, i * 100)
      }
    }
  },
  components: {
    Carousel,
    foot
  },
  created () {
    var self = this
    $.ajax({
      async: true,
      url: '/carousel/',
      type: 'GET',
      data: {},
      success: function (data) {
        var temp = []
        for (var i = 0; i < data.length; i++) {
          if (data[i].title.indexOf('≮') === -1) {
            temp.push({
              id: data[i].news_id,
              caption: '',
              caption2: data[i].title.split('≮')[0],
              bg: data[i].bg
            })
          } else {
            temp.push({
              id: data[i].news_id,
              caption: data[i].title.split('≮')[0],
              caption2: data[i].title.split('≮')[1],
              bg: data[i].bg
            })
          }
        }
        self.mainItem = temp
      }
    })
  },
  mounted () {
    var self = this
    if (!this.$root.adapt) {
      if (this.$root.height > 600) {
        this.showSolution()
        this.solutionStatus = true
      }
    }
    this.$root.eventHub.$on('showSolution', function () {
      if (!self.solutionStatus) {
        self.showSolution()
        self.solutionStatus = true
      }
    })
  }
}
</script>

<style scoped>
  .main-item p{
    color: white;
  }
  .main-item p:last-of-type{
    display: inline-block;
  }
  .main-item:first-of-type p:last-of-type{
    font-family: Xingkai!important;
  }
  .main-content{
    width: 100%;
    height: auto;
  }
  .page{
    overflow: hidden;
    text-align: center;
  }
  .page p{
    line-height: 160%;
  }
  .solution-circle{
    border-radius: 50%;
    background: #E6E6E6;
  }
  .solution-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .solution-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    -moz-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  @media only screen and (max-device-width: 767px) {
    /*.main-item:first-of-type p:first-of-type{*/
      /*font-size: 20px;*/
      /*line-height: 38px;*/
    /*}*/
    /*.main-item:first-of-type p:last-of-type{*/
      /*font-size: 40px;*/
    /*}*/
    /*.main-item:nth-of-type(2) p:first-of-type{*/
      /*font-size: 18px;*/
      /*line-height: 24px;*/
      /*padding: 0 85px;*/
    /*}*/
    /*.main-item:nth-of-type(2) p:last-of-type{*/
      /*font-size: 24px;*/
      /*padding: 0 60px;*/
    /*}*/
    /*.main-item:nth-of-type(3) p{*/
      /*font-size: 24px;*/
    /*}*/
    /*.main-item:nth-of-type(4) p:first-of-type{*/
      /*font-size: 20px;*/
      /*line-height: 32px;*/
    /*}*/
    /*.main-item:nth-of-type(4) p:last-of-type{*/
      /*font-size: 24px;*/
    /*}*/
    .page{
      width: 100%;
      height: 330px;
    }
    .page .title{
      font-size: 20px;
      margin-top: 24px;
    }
    .page .interpret{
      font-size: 16px;
      margin-top: 12px;
      max-width: 800px;
      padding: 0 20px;
      display: inline-block;
    }
    .page .learnMore{
      font-size: 16px;
      padding: 6px 12px;
      display: inline-block;
    }
    .solution-circle-bar{
      width: calc(100% - 40px);
      height: 102px;
      margin: 6px 20px;
      padding: 18px 0 0;
      overflow-x: scroll;
      overflow-y: hidden;
    }
    .solution-circle-bar::-webkit-scrollbar{
      display: none;
    }
    .solution-circle-wrapper{
      width: 200%;
      height: 75px;
    }
    .solution-circle{
      width: 75px;
      height: 75px;
      margin: 0 calc((100% - 450px) / 12);
    }
    .solution-circle p{
      line-height: 75px;
      font-size: 14px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    /*.main-item:first-of-type p:first-of-type, .main-item:nth-of-type(2) p:first-of-type{*/
      /*font-size: 24px;*/
    /*}*/
    /*.main-item:first-of-type p:last-of-type{*/
      /*font-size: 48px;*/
    /*}*/
    /*.main-item:nth-of-type(2) p:last-of-type{*/
      /*font-size: 30px;*/
      /*margin-top: 10px;*/
    /*}*/
    /*.main-item:nth-of-type(3) p{*/
      /*font-size: 30px;*/
    /*}*/
    /*.main-item:nth-of-type(4) p:first-of-type{*/
      /*font-size: 24px;*/
    /*}*/
    /*.main-item:nth-of-type(4) p:last-of-type{*/
      /*font-size: 28px;*/
    /*}*/
    .page{
      width: 100%;
      height: 640px;
    }
    .page .title{
      font-size: 24px;
      margin-top: 80px;
      padding: 0 100px;
    }
    .page .interpret{
      font-size: 20px;
      margin-top: 36px;
      max-width: 800px;
      padding: 0 60px;
      display: inline-block;
    }
    .page .learnMore{
      font-size: 16px;
      padding: 18px 12px 0;
      display: inline-block;
    }
    .solution-circle-bar{
      width: 450px;
      height: 240px;
      margin: 20px calc((100% - 450px) / 2);
      padding: 25px 0;
      overflow-x: auto;
      overflow-y: hidden;
    }
    .solution-circle{
      width: 100px;
      height: 100px;
      margin: 0 25px 30px;
    }
    .solution-circle p{
      line-height: 100px;
      font-size: 16px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 600px) {
    .solution-circle-bar{
      width: 540px;
      margin: 15px calc((100% - 540px) / 2);
    }
    .solution-circle{
      margin: 0 40px 40px;
    }
  }

  @media only screen and (min-device-width: 768px) and (-webkit-device-pixel-ratio: 1) {
    .page .learnMore:hover, .page .learnMore:active{
      color: #00BFFF;
    }
  }
</style>



